import artifactBuffs from "../data/artifacts/artifactBuffs";
import characters from "../data/characters";
import { elmts } from "../data/elements";
import monsters from "../data/monsters";
import weapons from "../data/weapons";
import { findWeapon, getElmtById, getElmtByName } from "../helpers/misc";
import { getArtSets } from "../helpers/processors";
import { initCharInfo, initWeapon } from "./database.helpers";

export function parseData(
  { name, weaponID, artIDs = [null, null, null, null, null], ...info },
  myWps,
  myArts
) {
  const char = { name, ...initCharInfo(info) };

  let wp;
  if (weaponID) {
    const existedWp = getElmtById(myWps, weaponID);
    if (existedWp) {
      const { ID, user, ...weapon } = existedWp;
      wp = {
        ...weapon,
        BCs: getMainWpBCs(existedWp)
      };
    }
  } else {
    const newWp = initWeapon({ type: characters[name].weapon });
    wp = { ...newWp, BCs: getMainWpBCs(newWp) };
  }

  const pieces = artIDs.map((id) => {
    const artP = getElmtById(myArts, id);
    if (artP) {
      const { user, ...info } = artP;
      return info;
    }
    return null;
  });
  const sets = getArtSets(pieces);
  const setCode = sets[0]?.bonusLv === 1 ? sets[0].code : null;
  const art = {
    pieces,
    sets,
    BCs: getMainArtBCs(setCode),
    subBCs: getSubArtBCs(setCode),
    subDCs: getSubArtDCs()
  };
  return [char, wp, art];
}

// also [helpers/processors, decode]
export function getMainWpBCs(weapon) {
  const result = [];
  const { buffs } = findWeapon(weapon);
  if (buffs) {
    for (const buff of buffs) {
      if (!buff.outdated && buff.affect !== "teammates") {
        const node = { activated: false, index: buff.index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type, j) => {
            if (type === "stacks") {
              node.inputs.push(1);
            } else if (type === "check") {
              node.inputs.push(false);
            } else if (type === "choices") {
              node.inputs.push(buff.values[j][0]);
            }
          });
        }
        result.push(node);
      }
    }
  }
  return result;
}

export function getBCsOfWp(weapon) {
  const BCs = [];
  const { buffs, code } = findWeapon(weapon);
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "self") {
        const node = { code, activated: false, refinement: 1, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((inpType, j) => {
            if (inpType === "choices") {
              node.inputs.push(buff.values[j][0]);
            }
          });
        }
        BCs.push(node);
      }
    });
  }
  return BCs;
}

// also [helpers/processors, decode]
export function getSubWpBCs(type, mainWpCode) {
  const result = [];
  for (const wp of weapons[type]) {
    if (wp.code !== mainWpCode) {
      result.push(...getBCsOfWp({ type, code: wp.code }));
    }
  }
  return result;
}

// also [helpers/processors, decode]
export function getMainArtBCs(setCode) {
  const result = [];
  if (artifactBuffs[setCode]) {
    artifactBuffs[setCode].forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type) => {
            if (type === "stacks") {
              node.inputs.push(buff.startsAt0 ? 0 : 1);
            } else if (type === "crystallize") {
              node.inputs.push("Pyro");
            }
          });
        }
        result.push(node);
      }
    });
  }
  return result;
}

export function getSubArtBCsByCode(code) {
  const result = [];
  artifactBuffs[code].forEach((buff, index) => {
    if (buff.affect !== "self") {
      const node = { code: +code, activated: false, index };
      if (buff.inputTypes) {
        node.inputs = [];
        buff.inputTypes.forEach((type) => {
          if (type === "crystallize") {
            node.inputs.push("Pyro");
          }
        });
      }
      result.push(node);
    }
  });
  return result;
}

export function getSubArtBCs(mainCode) {
  const result = [];
  for (const code in artifactBuffs) {
    if (code !== mainCode) {
      result.push(...getSubArtBCsByCode(code));
    }
  }
  return result;
}

// also [helpers/processors, decode]
export function getSubArtDCs() {
  return [{ code: 15, activated: false, index: 0, inputs: ["Pyro"] }];
}

// also [helpers/processors, decode]
export function initCharMCs(name, forSelf) {
  const result = { BCs: [], DCs: [] };
  const { buffs, debuffs } = characters[name];
  if (buffs) {
    for (const buff of buffs) {
      if (!buff.outdated && buff.affect !== (forSelf ? "teammates" : "self")) {
        const node = { activated: false, index: buff.index };
        if ((forSelf && buff.selfLabels) || (!forSelf && buff.labels)) {
          node.inputs = [...buff.inputs];
        }
        result.BCs.push(node);
      }
    }
  }
  // v2.4 - debuffs need inputs:
  // From self: AnemoMC C6 & Venti C6
  // Not from self: Eula E & Shenhe Q
  function getInputs(labels, inputTypes) {
    const inputs = [];
    labels.forEach((_, i) => {
      const type = inputTypes[i];
      if (type === "absorption") {
        inputs.push("Pyro");
      } else if (type === "text") {
        inputs.push(1); // level
      }
    });
    return inputs;
  }
  if (debuffs) {
    for (const debuff of debuffs) {
      if (debuff.outdated || (!forSelf && debuff.affect === "self")) {
        continue;
      }
      const node = { activated: false, index: debuff.index };
      const { selfLabels, labels, inputTypes } = debuff;
      if (forSelf && selfLabels) {
        node.inputs = getInputs(selfLabels, inputTypes);
      } else if (!forSelf && labels) {
        node.inputs = getInputs(labels, inputTypes);
      }
      result.DCs.push(node);
    }
  }
  return result;
}

export function initSubWpMCs() {
  return { BCs: {}, DCs: {} };
}

// also [decode]
export function initParty() {
  return [null, null, null];
}

export function initElmtMCs() {
  return {
    naAmpRxn: null,
    ampRxn: null,
    superconduct: false,
    resonance: []
  };
}

export function initCustomMCs() {
  return { BCs: [], DCs: [] };
}

export function initMonster() {
  return {
    name: monsters[0].name,
    inputs: []
  };
}

export function getMonsterInputs(name) {
  const { inputTypes, inputs } = getElmtByName(monsters, name);
  const result = [];
  if (inputTypes) {
    inputTypes.forEach((type, i) => {
      if (type === "variant") {
        result.push(inputs[i][0]);
      } else if (type === "check") {
        result.push(inputs[i]);
      }
    });
  }
  return result;
}

export function initTarget() {
  const result = { Level: 1, "Physical RES": 10 };
  for (const elmt of elmts) {
    result[elmt + " RES"] = 10;
  }
  return result;
}

export function initSettings() {
  return {
    on: false,
    sepCharInfo: false
  };
}

// also [helpers/processors, decode]
export function getVisionCount(char, party) {
  const count = {};
  count[characters[char.name].vision] = 1;
  for (const tm of party) {
    if (tm) {
      const { vision } = characters[tm.name];
      count[vision] = (count[vision] || 0) + 1;
    }
  }
  return count;
}

// also [helpers/processors, decode]
export function getWpRange(party) {
  const result = {};
  for (const tm of party) {
    if (tm) result[characters[tm.name].weapon] = 1;
  }
  return result;
}

export function adjustTarget(target, monster) {
  const { res, addResBn } = getElmtByName(monsters, monster.name);
  for (const type in res) {
    if (type === "Elemental RES") {
      for (let elmt of elmts) {
        target[elmt + " RES"] = res[type];
      }
    } else {
      target[type] = res[type];
    }
  }
  if (addResBn) {
    addResBn({ target, inputs: monster.inputs });
  }
}

// also [SetupManage/Settings/Index & SetupCtrl]
export function getSetupInfo({
  name = "Setup 1",
  ID = Date.now(),
  type = "original"
}) {
  return { name, ID, type };
}

// also [SetupManage/Settings/Index]
export function getNewSetupName(setups) {
  const existedIs = [1, 2, 3, 4];
  for (const st of setups) {
    const parts = st.name.split(" ");
    if (parts.length === 2 && parts[0] === "Setup" && !isNaN(parts[1])) {
      const i = existedIs.indexOf(+parts[1]);
      if (i !== -1) existedIs.splice(i, 1);
    }
  }
  return "Setup " + existedIs[0];
}
