import { createContext } from "react";
import { useSelector } from "react-redux";
import getBuffedStats from "../../calculators/buffedStats";
import getDmg from "../../calculators/damage";
import characters from "../../data/characters";
import { getFinalInfusion, getThisChar } from "../../helpers/processors";
import { calculator } from "../../redux/calculatorSlice";
import { Box } from "../../styledCpns/Layout";
import CalcChar from "./CalcChar";
import DmgResults from "./DmgResults";
import Modifiers from "./Modifiers";
import SetupManager from "./SetupManager";
import { Card, StyledCalculator } from "./styles";

export const ResultCt = createContext();

export default function Calculators() {
  const {
    setups,
    char,
    selfMCs,
    wpRack,
    subWpMCs,
    artChest,
    parties,
    elmtMCs,
    customMCs,
    target
  } = useSelector(calculator);

  let charData;
  const allATTRs = [];
  const allArtSBnes = [];
  const allRxnBnes = [];
  const allInfusion = [];
  const allDmg = [];
  if (char) {
    console.log("run");
    charData = characters[char.name];
    for (let i = 0; i < setups.length; i++) {
      const thisChar = getThisChar(char, i);
      const finalInfusion = getFinalInfusion(
        thisChar,
        selfMCs[i].BCs,
        charData.vision,
        parties[i]
      );
      allInfusion.push(finalInfusion);
      const [ATTRs, bonuses, rxnBnes, artSBnes] = getBuffedStats(
        charData,
        thisChar,
        selfMCs[i].BCs,
        wpRack[i],
        subWpMCs[i].BCs,
        artChest[i],
        elmtMCs[i].resonance,
        parties[i],
        customMCs[i].BCs,
        finalInfusion
      );
      allATTRs.push(ATTRs);
      allArtSBnes.push(artSBnes);
      allRxnBnes.push(rxnBnes);
      allDmg.push(
        getDmg(
          thisChar,
          selfMCs[i],
          parties[i],
          artChest[i].subDCs,
          ATTRs,
          bonuses,
          rxnBnes,
          customMCs[i].DCs,
          finalInfusion,
          elmtMCs[i],
          target
        )
      );
    }
  }
  return (
    <ResultCt.Provider
      value={{ allInfusion, allATTRs, allArtSBnes, allRxnBnes, allDmg }}
    >
      <StyledCalculator>
        <Box className="flex" height="98%">
          <CalcChar />
          {char ? <Modifiers /> : <Card bgColor="cardBg1" />}
          <SetupManager />
          {char ? <DmgResults /> : <Card bgColor="cardBg3" />}
        </Box>
      </StyledCalculator>
    </ResultCt.Provider>
  );
}
