import "./styles.css";
import "./custom.css";
import { useState, useEffect, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AbsoluteBox } from "./styledCpns/Layout";
import NavBar from "./components/NavBar";
import MyCharacters from "./components/MyCharacters";
import MyWeapons from "./components/MyWeapons";
import MyArtifacts from "./components/MyArtifacts";
import MySetups from "./components/MySetups";
import Calculator from "./components/Calculator";
import Intro from "./components/Canopy/Intro";
import Importing from "./components/Canopy/Importing";
import Outdates from "./components/Canopy/Outdates";
import adjustDB from "./helpers/adjustDB";
import { UPLOAD_DB } from "./redux/databaseSlice";
import { UPLOAD_SETUPS } from "./redux/setupsSlice";

export default function App() {
  const { tab, importType } = useSelector((state) => state.ui);
  const [introOn, setIntroOn] = useState(true);
  const [outdates, setOutdates] = useState(null);
  const dispatch = useDispatch();

  const loadData = useCallback(
    (data) => {
      const [db, outdates] = adjustDB(data);
      dispatch(
        UPLOAD_DB({
          myChars: db.Characters,
          myWps: db.Weapons,
          myArts: db.Artifacts
        })
      );
      dispatch(UPLOAD_SETUPS(db.Setups));
      if (outdates) setOutdates(outdates);
    },
    [dispatch]
  );

  useEffect(() => {
    const data = localStorage.getItem("GDC_Data");
    if (data)
      try {
        loadData(JSON.parse(data));
      } catch (err) {
        console.log(err.message);
      }
  }, []);

  return (
    <div className="App flex-col">
      <NavBar
        current={tab}
        setIntroOn={setIntroOn}
        loadData={loadData}
        outdates={outdates}
      />
      <div className="grow-1 flex-center relative">
        <Calculator />
        {tab !== "Calculator" && (
          <AbsoluteBox top={0} left={0} zIndex={2} className="full-w full-h">
            {
              {
                Characters: <MyCharacters />,
                Weapons: <MyWeapons />,
                Artifacts: <MyArtifacts />,
                Setups: <MySetups />
              }[tab]
            }
          </AbsoluteBox>
        )}
        {introOn && <Intro close={() => setIntroOn(false)} />}
        {importType && <Importing />}
        {outdates && (
          <Outdates outdates={outdates} close={() => setOutdates(null)} />
        )}
      </div>
    </div>
  );
}
