import { createSlice } from "@reduxjs/toolkit";
import { artTypes } from "../data/misc";
import { processNumInp } from "../helpers/processors";
import {
  initCharInfo,
  initWeapon,
  compareArts,
  compareWps
} from "./database.helpers";
import {
  getElmtById,
  getElmtByName,
  getIndexByID,
  getIndexByName
} from "../helpers/misc";

export const databaseSlice = createSlice({
  name: "database",
  initialState: {
    myChars: [],
    myWps: [],
    myArts: []
  },
  reducers: {
    UPLOAD_DB: (_, action) => {
      return action.payload;
    },
    ADD_CHAR: (state, action) => {
      const { name, weapon } = action.payload;
      const weaponID = Date.now();
      state.myChars.unshift({
        name,
        ...initCharInfo({}),
        weaponID,
        artIDs: [null, null, null, null, null]
      });
      state.myWps.unshift({
        ID: weaponID,
        user: name,
        ...initWeapon({ type: weapon })
      });
    },
    SORT_CHARS: (state, action) => {
      state.myChars = action.payload.map((index) => state.myChars[index]);
    },
    TOGGLE_CONS: (state, action) => {
      const { name, consIndex } = action.payload;
      const char = getElmtByName(state.myChars, name);
      char.constellation =
        char.constellation === consIndex + 1 ? consIndex : consIndex + 1;
    },
    CHANGE_TALENT: (state, action) => {
      const { name, type, level } = action.payload;
      getElmtByName(state.myChars, name)[type] = level;
    },
    LEVEL_CHAR: (state, action) => {
      const { name, level } = action.payload;
      const maxLv = +level.slice(-2);
      const char = getElmtByName(state.myChars, name);
      char.level = level;
      char.ascension = maxLv === 20 ? 0 : maxLv / 10 - 3;
    },
    REMOVE_CHAR: (state, action) => {
      const { name, setCharIndex } = action.payload;
      const { myChars, myWps, myArts } = state;
      const charIndex = getIndexByName(myChars, name);
      const { weaponID, artIDs } = myChars[charIndex];
      getElmtById(myWps, weaponID).user = null;
      for (const id of artIDs) {
        if (id) getElmtById(myArts, id).user = null;
      }
      myChars.splice(charIndex, 1);
      if (charIndex > 0 && charIndex === myChars.length) {
        setCharIndex((prev) => prev - 1);
      }
    },
    SWITCH_WP: (state, action) => {
      const { newUser, newID, oldUser, oldID } = action.payload;
      const { myWps, myChars } = state;
      getElmtById(myWps, newID).user = oldUser;
      getElmtById(myWps, oldID).user = newUser;
      getElmtByName(myChars, oldUser).weaponID = newID;
      if (newUser) {
        getElmtByName(myChars, newUser).weaponID = oldID;
      }
    },
    SWITCH_ART: (state, action) => {
      const { newUser, newID, oldUser, oldID, pos } = action.payload;
      const { myArts, myChars } = state;
      getElmtById(myArts, newID).user = oldUser;
      if (oldID) {
        getElmtById(myArts, oldID).user = newUser;
      }
      getElmtByName(myChars, oldUser).artIDs[pos] = newID;
      if (newUser) {
        getElmtByName(myChars, newUser).artIDs[pos] = oldID;
      }
    },
    UNEQUIP_ART: (state, action) => {
      const { user, artID, pos } = action.payload;
      getElmtByName(state.myChars, user).artIDs[pos] = null;
      getElmtById(state.myArts, artID).user = null;
    },
    // wp
    ADD_WP: (state, action) => {
      const { ID, item } = action.payload;
      state.myWps.unshift({ ID, user: null, ...item });
    },
    SORT_WPS: (state) => {
      state.myWps.sort(compareWps);
    },
    SWAP_WP_USER: (state, action) => {
      const { newUser, wpID, oldUser } = action.payload;
      const { myChars, myWps } = state;
      getElmtById(myWps, wpID).user = newUser;
      const newChar = getElmtByName(myChars, newUser);
      const { weaponID } = newChar;
      getElmtById(myWps, weaponID).user = oldUser;
      if (oldUser) {
        getElmtByName(myChars, oldUser).weaponID = weaponID;
      }
      newChar.weaponID = wpID;
    },
    REMOVE_WP: (state, action) => {
      const { ID, user, type } = action.payload;
      const { myWps, myChars } = state;
      myWps.splice(getIndexByID(myWps, ID), 1);
      if (user) {
        const newWpID = Date.now();
        myWps.unshift({
          ID: newWpID,
          user,
          ...initWeapon({ type })
        });
        getElmtByName(myChars, user).weaponID = newWpID;
      }
    },
    UPGRADE_WP: (state, action) => {
      const { ID, level } = action.payload;
      getElmtById(state.myWps, ID).level = level;
    },
    REFINE_WP: (state, action) => {
      const { ID, rfm } = action.payload;
      getElmtById(state.myWps, ID).refinement = rfm;
    },
    // art
    ADD_ART: (state, action) => {
      state.myArts.unshift(action.payload);
    },
    SORT_ARTS: (state) => {
      state.myArts.sort(compareArts);
    },
    SWAP_ART_USER: (state, action) => {
      const { newUser, artID, artType, oldUser } = action.payload;
      const { myChars, myArts } = state;
      getElmtById(myArts, artID).user = newUser;
      const { artIDs } = getElmtByName(myChars, newUser);
      const pos = artTypes.indexOf(artType);
      if (artIDs[pos]) {
        getElmtById(myArts, artIDs[pos]).user = oldUser;
      }
      if (oldUser) {
        getElmtByName(myChars, oldUser).artIDs[pos] = artIDs[pos];
      }
      artIDs[pos] = artID;
    },
    REMOVE_ART: (state, action) => {
      const { ID, user, type } = action.payload;
      const { myArts, myChars } = state;
      myArts.splice(getIndexByID(myArts, ID), 1);
      if (user) {
        const pos = artTypes.indexOf(type);
        getElmtByName(myChars, user).artIDs[pos] = null;
      }
    },
    ENHANCE_ART: (state, action) => {
      const { ID, level } = action.payload;
      getElmtById(state.myArts, ID).level = level;
    },
    ENHANCE_ART_BY_4: (state, action) => {
      const { ID, rarity } = action.payload;
      const { myArts } = state;
      const art = getElmtById(myArts, ID);
      art.level = Math.min(rarity === 4 ? 16 : 20, art.level + 4);
    },
    ENHANCE_ART_TO_MAX: (state, action) => {
      const { ID, rarity } = action.payload;
      getElmtById(state.myArts, ID).level = rarity === 4 ? 16 : 20;
    },
    CHANGE_ART_MS_TYPE: (state, action) => {
      const { ID, type } = action.payload;
      getElmtById(state.myArts, ID).mainSType = type;
    },
    CHANGE_ART_SS_TYPE: (state, action) => {
      const { ID, ssIndex, type } = action.payload;
      for (const art of state.myArts) {
        if (art.ID === ID) {
          art.subS[ssIndex].type = type;
          break;
        }
      }
    },
    CHANGE_ART_SS_VAL: (state, action) => {
      const { ID, ssIndex, value } = action.payload;
      for (const art of state.myArts) {
        if (art.ID === ID) {
          const subS = art.subS[ssIndex];
          const processedValue = processNumInp(value, subS.val, 9999);
          subS.val = processedValue;
          break;
        }
      }
    },
    OVERWRITE_ART: (state, action) => {
      const { ID, ...stats } = action.payload;
      const { myArts } = state;
      const index = getIndexByID(myArts, ID);
      myArts[index] = { ...myArts[index], ...stats };
    }
  }
});

export const {
  UPLOAD_DB,
  ADD_CHAR,
  SORT_CHARS,
  TOGGLE_CONS,
  CHANGE_TALENT,
  LEVEL_CHAR,
  REMOVE_CHAR,
  SWITCH_WP,
  SWITCH_ART,
  UNEQUIP_ART,
  ADD_WP,
  SORT_WPS,
  SWAP_WP_USER,
  REMOVE_WP,
  UPGRADE_WP,
  REFINE_WP,
  ADD_ART,
  SORT_ARTS,
  SWAP_ART_USER,
  REMOVE_ART,
  ENHANCE_ART,
  ENHANCE_ART_BY_4,
  ENHANCE_ART_TO_MAX,
  CHANGE_ART_MS_TYPE,
  CHANGE_ART_SS_TYPE,
  CHANGE_ART_SS_VAL,
  OVERWRITE_ART
} = databaseSlice.actions;

export default databaseSlice.reducer;
