import { findArtifact, findWeapon } from "../helpers/misc";

export function initCharInfo(info) {
  return {
    level: info.level || "1/20",
    ascension: info.ascension || 0,
    "Normal Attack": info["Normal Attack"] || 1,
    "Elemental Skill": info["Elemental Skill"] || 1,
    "Elemental Burst": info["Elemental Burst"] || 1,
    constellation: info.constellation || 0
  };
}

// also [Picker/ItemPicker]
export function initWeapon({ type, code }) {
  if (!code) {
    code = {
      Bow: 11,
      Catalyst: 36,
      Claymore: 59,
      Polearm: 84,
      Sword: 108
    }[type];
  }
  return { type, code, level: "1/20", refinement: 1 };
}

// also [Picker/ItemPicker]
export function initArtPiece({ type, code, rarity }) {
  return {
    type,
    code,
    rarity,
    level: 0,
    mainSType: type === "flower" ? "HP" : type === "plume" ? "ATK" : "ATK%",
    subS: [
      { type: "DEF", val: 0 },
      { type: "DEF%", val: 0 },
      { type: "CRIT Rate", val: 0 },
      { type: "CRIT DMG", val: 0 }
    ]
  };
}

export function compareWps(a, b) {
  const rA = findWeapon(a).rarity;
  const rB = findWeapon(b).rarity;
  if (rA !== rB) return rB - rA;
  const [fA, sA] = a.level.split("/").map((lv) => +lv);
  const [fB, sB] = b.level.split("/").map((lv) => +lv);
  if (fA !== fB) {
    return fB - fA;
  }
  if (a.type !== b.type) {
    const type = {
      Bow: 5,
      Catalyst: 4,
      Polearm: 3,
      Claymore: 2,
      Sword: 1
    };
    return type[b.type] - type[a.type];
  }
  return sB - sA;
}

export function compareArts(a, b) {
  if (a.level !== b.level) {
    return b.level - a.level;
  }
  if (a.type !== b.type) {
    const type = {
      flower: 5,
      plume: 4,
      sands: 3,
      goblet: 2,
      circlet: 1
    };
    return type[b.type] - type[a.type];
  }
  const aName = findArtifact({ code: a.code }).name;
  const bName = findArtifact({ code: b.code }).name;
  return bName.localeCompare(aName);
}
