import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { artMS } from "../../../data/artifacts/config";
import { getElmtById, pctOf } from "../../../helpers/misc";
import { processNumInp } from "../../../helpers/processors";
import {
  CHANGE_ART_MS_TYPE,
  CHANGE_ART_SS_TYPE,
  CHANGE_ART_SS_VAL,
  ENHANCE_ART,
  SET_ART_PIECE
} from "../../../redux/calculatorSlice";
import { ADD_ART, OVERWRITE_ART } from "../../../redux/databaseSlice";
import { Text } from "../../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { ButtonBar, Confirmation } from "../../CommonCpns";
import { Collapse } from "../../CommonCpns/HiddenSpace";
import ArtSubstats from "../../Readers/ArtifactCard/ArtSubstats";

export default function ArtPieceInfo({ infoOn, pieces, tabNo, setPickerOn }) {
  return (
    <Collapse in={infoOn}>
      {pieces[tabNo] ? (
        <FilledCard
          artP={pieces[tabNo]}
          tabNo={tabNo}
          setPickerOn={setPickerOn}
        />
      ) : (
        <EmptyCard setPickerOn={setPickerOn} />
      )}
    </Collapse>
  );
}

export function FilledCard({ artP, tabNo, setPickerOn }) {
  const { rarity = 5 } = artP;
  const msType = artP.mainSType;
  const availableMSTypes = artMS[artP.type];
  const isFoP = ["flower", "plume"].includes(artP.type);
  const msValues = availableMSTypes[msType][rarity];
  const { myArts } = useSelector((state) => state.database);
  const [modal, setModal] = useState(0);
  const dispatch = useDispatch();
  const pieceIndex = tabNo;

  const save = () => {
    if (getElmtById(myArts, artP.ID)) {
      setModal(2);
    } else {
      dispatch(ADD_ART({ user: null, ...artP }));
      setModal(1);
    }
  };
  const switchArtP = () => setPickerOn(true);
  return (
    <div className="pt-4">
      <div className="pl-4 flex align-start">
        <Box bgColor="cardBg3" borderRadius="50%">
          <Select
            color={rarity}
            fontSize="1.125rem"
            fontWeight={700}
            className="appearance-none px-2 pt-2 pb-1 pointer"
            value={"+" + artP.level}
            onChange={(e) =>
              dispatch(
                ENHANCE_ART({
                  pieceIndex,
                  level: +e.target.value.substr(1)
                })
              )
            }
          >
            {[...Array(rarity === 5 ? 21 : 17).keys()].map((_, lv) => (
              <option key={lv}>+{lv}</option>
            ))}
          </Select>
        </Box>
        <div className="ml-3">
          {isFoP ? (
            <Text className="pl-5 pt-1" variant="h6">
              {msType}
            </Text>
          ) : (
            <div className="py-1">
              <Select
                fontSize="1.125rem"
                color="white"
                className="pl-5 appearance-none bg-for-icon white-arrow-bg"
                value={msType}
                onChange={(e) =>
                  dispatch(
                    CHANGE_ART_MS_TYPE({ pieceIndex, type: e.target.value })
                  )
                }
              >
                {Object.keys(availableMSTypes).map((type) => (
                  <option key={type}>{type}</option>
                ))}
              </Select>
            </div>
          )}
          <Text className="pl-5" variant="h3" color={rarity}>
            {msValues[artP.level] + pctOf(msType)}
          </Text>
        </div>
      </div>
      <div className="px-2 pb-1">
        <ArtSubstats
          mutable={true}
          substats={artP.subS}
          changeSSType={(type, ssIndex) =>
            dispatch(CHANGE_ART_SS_TYPE({ pieceIndex, ssIndex, type }))
          }
          changeSSVal={(value, ssIndex, oldValue) =>
            dispatch(
              CHANGE_ART_SS_VAL({
                pieceIndex,
                ssIndex,
                value: processNumInp(value, oldValue, 9999)
              })
            )
          }
          msType={msType}
          rarity={rarity}
        />
      </div>
      <div className="mt-3 flex justify-evenly align-center">
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-trash-alt"
          variant="negative"
          onClick={() =>
            dispatch(SET_ART_PIECE({ pieceIndex, newPiece: null }))
          }
        />
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-save"
          variant="neutral"
          onClick={save}
        />
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-sync-alt"
          variant="positive"
          fontSize="1.25rem"
          onClick={switchArtP}
        />
      </div>
      {modal > 0 && (
        <ConfirmSave modal={modal} artP={artP} close={() => setModal(0)} />
      )}
    </div>
  );
}

function ConfirmSave({ modal, artP, close }) {
  const { myArts } = useSelector((state) => state.database);
  const dispatch = useDispatch();
  const existed = getElmtById(myArts, artP.ID);
  let xtra = ".";
  if (existed) {
    xtra = (
      <>
        , currently used by <b>{existed.user}</b>.
      </>
    );
  }
  let message;
  if (modal === 1) {
    message = "Successfully saved to My Artifacts.";
  } else {
    message = (
      <>
        This one is already in My Artifacts{xtra} Do you want to overwrite its
        Stats?
      </>
    );
  }
  return (
    <Confirmation
      message={message}
      otherBtn={modal === 1 ? { text: "Close" } : null}
      confirm={() => {
        if (modal === 1) {
          close();
        } else {
          dispatch(OVERWRITE_ART(artP));
        }
      }}
      close={close}
    />
  );
}

export function EmptyCard({ setPickerOn }) {
  return (
    <Box height="18.05rem">
      <div className="py-4 flex-center">
        <Text variant="h6">Not equipping any artifact</Text>
      </div>
      <ButtonBar
        texts={["Switch"]}
        variants={["positive"]}
        handlers={[() => setPickerOn(true)]}
      />
    </Box>
  );
}
