import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useCloseModal } from "../../../../helpers/hooks";
import { deepCopy, getIndexByID } from "../../../../helpers/misc";
import { filterAllMCs, getThisChar } from "../../../../helpers/processors";
import { calculator } from "../../../../redux/calculatorSlice";
import { CHOOSE_SETUP, SAVE_SETUP } from "../../../../redux/setupsSlice";
import { CHANGE_TAB, CLOSE_SETTINGS } from "../../../../redux/uiSlice";
import { Text } from "../../../../styledCpns/DataDisplay";
import { TextInput } from "../../../../styledCpns/Inputs";
import { Box, ModalCarpet } from "../../../../styledCpns/Layout";
import { ButtonBar, ShareLayout } from "../../../CommonCpns";

export function SaveCtrl({ savedSetup: { name, ID, index }, close }) {
  const { char } = useSelector(calculator);
  const { mySetups } = useSelector((state) => state.setups);
  const existedI = getIndexByID(mySetups, ID);
  const isExisted = existedI >= 0;
  const [input, setInput] = useState(
    isExisted ? mySetups[existedI].name : char.name + " - " + name
  );
  const data = useProcessData(index);
  const dispatch = useDispatch();

  const saveSetup = () => {
    let newID = ID;
    let complexIndex = -1;
    if (isExisted && mySetups[existedI].type === "combined") {
      complexIndex = mySetups.findIndex(
        (st) => st.type === "complex" && st.allIDs[char.name] === ID
      );
      if (complexIndex >= 0) {
        newID = mySetups[complexIndex].ID;
      }
    }
    dispatch(SAVE_SETUP({ complexIndex, ID, name: input, existedI, data }));
    dispatch(CHOOSE_SETUP(newID));
    dispatch(CHANGE_TAB("Setups"));
    dispatch(CLOSE_SETTINGS());
  };

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-4 flex-col white-glow-shadow max-width-95"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text
          className="mb-2 mx-auto"
          variant="h5"
          color="orange"
          fontWeight={700}
        >
          {existedI >= 0 ? "Modify OLD" : "Save NEW"} Setup
        </Text>
        <TextInput
          className="mt-1 mb-5 px-3 pt-3 pb-2"
          type="text"
          borderRadius="0.25rem"
          fontSize="1.5rem"
          align="center"
          value={input}
          onChange={(e) => {
            const { value } = e.target;
            if (value.length <= 32) setInput(e.target.value);
          }}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              saveSetup();
              close();
            }
          }}
        />
        <ButtonBar
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              saveSetup();
              close();
            }
          ]}
        />
      </Box>
    </ModalCarpet>
  );
}

export function SharedCtrl({ sharedSetup: { name, index }, close }) {
  return (
    <ShareLayout setupName={name} data={useProcessData(index)} close={close} />
  );
}

function useProcessData(index) {
  const {
    char,
    selfMCs,
    wpRack,
    subWpMCs,
    parties,
    elmtMCs,
    customMCs,
    target
  } = useSelector(calculator);
  const thisChar = getThisChar(char, index);
  const art = deepCopy(useSelector(calculator).artChest[index]);
  for (const p of art.pieces) {
    if (p) delete p.ID;
  }
  return {
    char: thisChar,
    ...filterAllMCs({
      char: thisChar,
      selfMCs: selfMCs[index],
      weapon: wpRack[index],
      subWpMCs: subWpMCs[index],
      art,
      party: parties[index],
      elmtMCs: elmtMCs[index]
    }),
    customMCs: customMCs[index],
    target
  };
}
